<template>
  <div>
    <el-upload
      :before-upload="validateFile"
      :on-success="handleSuccess"
      :on-error="handleError"
      multiple
      action="https://umaru.net/image/hosting/upload"
      accept="image/*"
      list-type="picture-card"
    >
      <el-button size="small" type="primary">点击上传</el-button>
    </el-upload>
    <URLList
      v-for="([url, name, uuid, deleteId], index) in fileList"
      :key="index"
      :url="url"
      :file-name="name"
      :uuid="uuid"
      :delete-id="deleteId"
      />
  </div>
</template>

<!--suppress JSUnusedGlobalSymbols -->
<script>
import { Message } from 'element-ui'
import URLList from '@/components/URLList'

export default {
  name: 'Upload',
  components: {
    URLList
  },
  data () {
    return {
      fileList: []
    }
  },
  methods: {
    validateFile (file) {
      const isImageFile = file.type.startsWith('image/')
      const isLessThan5Mb = file.size <= 5 * 1024 * 1024
      if (!isImageFile) {
        Message.warning('只支持图片类型的文件上传')
      }
      if (!isLessThan5Mb) {
        Message.warning('只支持5Mb以下的文件上传')
      }
      return isImageFile && isLessThan5Mb
    },
    handleSuccess (response, file) {
      this.fileList.push([file.url, file.name, response.name, response.id])
    },
    handleError (error, file) {
      Message.error(`${file.name}上传失败`)
      console.error(error)
    }
  }
}
</script>

