<template>
  <div class="container">
    <!--suppress HtmlUnknownTarget -->
    <el-image :src="url" fit="cover"/>
    <div class="right">
      <div>
        <p>URL: {{ getURL }}</p>
        <span :data-clipboard-text="getURL">[复制链接]</span>
      </div>
      <div>
        <p>HTML: {{ getHTML }}</p>
        <span :data-clipboard-text="getHTML">[复制链接]</span>
      </div>
      <div>
        <p>BBCode: {{ getBBCode }}</p>
        <span :data-clipboard-text="getBBCode">[复制链接]</span>
      </div>
      <div>
        <p>Markdown: {{ getMarkdown }}</p>
        <span :data-clipboard-text="getMarkdown">[复制链接]</span>
      </div>
      <div>
        <p>Remove Link: {{ getRemoveLink }}</p>
        <span :data-clipboard-text="getRemoveLink">[复制链接]</span>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard'

// noinspection JSUnusedGlobalSymbols
export default {
  name: 'URLList',
  props: {
    url: {
      type: String,
      required: true
    },
    fileName: {
      type: String,
      required: true
    },
    uuid: {
      type: String,
      require: true
    },
    deleteId: {
      type: String,
      required: true
    }
  },
  computed: {
    getURL () {
      return `https://umaru.net/image/${this.uuid}/${this.fileName}`
    },
    getHTML () {
      // noinspection HtmlRequiredAltAttribute
      return `<img src="${this.getURL}" />`
    },
    getBBCode () {
      return `[img]${this.getURL}[/img]`
    },
    getMarkdown () {
      return `![${this.fileName}](${this.getURL})`
    },
    getRemoveLink () {
      return `https://umaru.net/image/delete/${this.deleteId}`
    }
  },
  created () {
    // eslint-disable-next-line no-new
    new Clipboard('.right>div>span')
  }
}
</script>

